.taskContainer {
	display: flex;
	justify-content: space-between;
	min-height: 60px;
	border: 1px solid #bbbbbb;
	border-radius: 5px;
	background-color: #ffffff;
}

.taskTextWrapper {
	padding: 10px;
}

.taskTitle {
	margin-bottom: 5px;
	color: #1677ff;
	font-size: 16px;
	font-weight: 500;
}

.taskNotes {
	font-size: 14px;
	font-weight: 300;
}

.taskToolsWrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-width: 100px;
	padding: 10px;
	border-radius: 0 5px 5px 0;
	background-color: #5a5a5a;
	color: #ced4da;
	font-size: 12px;
}

.taskDeadline {
	margin-bottom: 15px;
	color: #ffa500;
	font-weight: 600;
	text-align: center;
}

.taskTools {
	display: flex;
	column-gap: 5px;
	justify-content: space-between;

	p {
		flex-basis: 50%;
		text-align: center;
	}

	.button {
		flex-basis: 50%;
		border: 1px solid #ced4da;
		border-radius: 20px;
		background-color: transparent;
		color: currentColor;
		font-size: 12px;
		transition:
			border-color 250ms,
			background-color 250ms,
			color 250ms;

		&:hover,
		&:focus {
			&:first-child {
				border-color: #008000;
				background-color: #008000;
				color: #ffffff;
			}
			&:last-child {
				border-color: #ff0000;
				background-color: #ff0000;
				color: #ffffff;
			}
		}
	}
}
